* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #393838;
    border-radius: 12px;
    font-size: 14px;
}

.header {

    &__logo {
        padding-top: 10px;
        display: flex;
        justify-content: center;
    }

    &__text {
        font-family: 'Patua One', cursive;
        color: #fff;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 1.5;
        text-align: center;
        margin-top: 30px;
        padding-bottom: 30px;
        font-size: 26px;
    }
}

.nav {
    font-family: 'Spartan', sans-serif;
    display: flex;
    justify-content: space-between;
    background-color: #fde700;
    color: rgb(0, 0, 0);
    padding: 18px 100px;

    a {
        list-style-type: none;
        width: 190px;
        height: 30px;
        background-color: #fff;
        border-radius: 50px;
        text-align: center;
        line-height: 30px;
        text-transform: uppercase;
        font-weight: 700;
        cursor: pointer;
        text-decoration: none;

        &:hover {
            background-color: #393838;
            color: #fde700;
            transition: .2s linear;
        }

        &.active {
            background-color: #393838;
            color: #fde700;
            transition: .2s linear;
        }

    }
}

.main {
    color: #fff;

    &__title {
        font-family: 'Patua One', cursive;
        text-align: center;
        margin: 30px 100px;
        font-size: 24px;
    }

    &__text {
        font-family: 'Patua One', cursive;
        text-align: center;
        margin: 30px 100px;
        font-size: 22px;
    }

    &__for {
        display: flex;
        justify-content: space-around;
        padding: 15px 0;
        background-color: #fde700;
        color: rgb(0, 0, 0);

        &-1,
        &-2 {
            font-size: 24px;
            line-height: 1.2;
            width: 400px;
            text-align: left;
            font-weight: 700;
        }
    }

    &__ul {
        color: rgb(0, 0, 0);
        background-color: #fde700;
        display: flex;
        justify-content: space-around;
        padding-bottom: 20px;
        border-bottom: 1px solid #393838;

        &-1,
        &-2 {
            font-size: 20px;
            line-height: 1.2;
            width: 400px;
            text-align: left;
        }
    }

    &__promo {
        text-align: center;
        margin: 30px 0;
    }

    li {
        padding: 5px 0;
    }
}

.questions {
    color: #fde700;
    font-family: 'Patua One', cursive;

    &__text-1 {
        margin: 30px 0 30px 0;
        text-align: center;
        color: #fff;
        font-size: 30px;
    }

    &__text {
        margin-top: 50px;
        text-align: center;
        color: #fff;
        font-size: 30px;
    }

    .table {
        text-transform: uppercase;
        font-size: 28px;
        font-weight: 700;
        margin: 60px auto;

        td {
            padding: 20px;
            border: 1px solid yellow;
        }

        td:hover {
            background-color: yellow;
            color: black;
            transition: .3s;
        }
    }

    &__promo {
        padding-top: 40px;
        text-align: center;
        padding-bottom: 30px;
        background-color: #fde700;
    }
}

.footer {
    background-color: #fde700;
    padding: 20px 10px 10px 10px;
    border-radius: 0 0 12px 12px;

    &__text {
        text-align: center;
        font-size: 18px;
        margin-bottom: 10px;
    }

    &__tel {
        color: rgb(177, 95, 84);
        text-decoration: none;
    }

    &__social {
        position: relative;
        display: flex;
        justify-content: center;

        img {
            margin: 10px 20px;
            width: 40px;
            height: 40px;

            &:hover {
                transform: scale(1.3);
                transition: .2s linear;
            }
        }

    }
}

/*    form     */

.overlay {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}

.form {
    width: 600px;
    height: 580px;
    background-color: white;
    position: fixed;
    top: 20%;
    left: 35%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;

    .p1 {
        font-size: 32px;
        text-align: center;
        font-weight: 600;
        margin-top: 80px;
    }

    .p2 {
        font-size: 20px;
        text-align: center;
        margin-top: 10px;
    }

    img {
        position: absolute;
        top: 10px;
        right: 10px;
        cursor: pointer;
    }

    .input {
        text-align: center;
        margin-top: 40px;
        width: 440px;
        height: 60px;
        border-radius: 10px;
        background-color: white;
        border: 1px solid rgb(190, 182, 182);
    }

    .button {
        margin-top: 30px;
        width: 450px;
        height: 80px;
        border-radius: 10px;
        text-align: center;
        font-size: 24px;
        font-weight: 600;
        background-color: white;
        border: none;
        background-color: #fde700;
        cursor: pointer;
    }

}

